export const postReducer = (state = initialState, action) => {
  switch (action.type) {

    case LOAD_LISTENER:
      return {
        ...state,
        loadListener:action.payload
      }


    case SET_PLATFORM_DEVICE:
      return {
        ...state,
        DevicePlatformUser:action.payload
      }


    case UPDATE_ACTIVE_GIFTS:
      return {
        ...state,
        activeGift:action.payload
      }

    case SET_TYPE_DEVICE:
      return {
        ...state,
        typeDeviceVK:action.payload
      }

    case MODIFY_TMP_UPDATE:
      return {
        ...state,
        modifyTmp:action.payload.ModifyList,
        modifyTmpPrice:action.payload.ModifyPrice,
      }

    case MODIFY_TMP_UPDATE_BUTTON_SET_PRODUCT:
      return {
        ...state,
        activeButtonSetProduct:action.payload,
      }

    case UPDATE_GIFTS_LIST:
      return {
        ...state,
        giftsList:action.payload
      }

    case UPDATE_THREEFORTWO_LIST:
      return {
        ...state,
        TreeForTwoList:action.payload
      }

    case UPDATE_THREEFORTWO_ACTIVE:
      return {
        ...state,
        ThreeForTwoActive:action.payload,
        ThreeForTwoListProduct:action.listProduct,
        SumDiscountThreeForTwo:action.sumDiscount
      }

    case UPDATE_PUSHNOTICE_LIST:
      return {
        ...state,
        pushNoticeList:action.payload
      }
    case UPDATE_COUNT_PUSHNOTICE_LIST:
      return {
        ...state,
        pushNoticeCountNew:action.payload
      }
    case SET_DEFAULT_STATE:
      return {
        ...state,
        basket:[],
        basketOff:[],
        basketTopping:[],
        basketSum:0,

        promokodeData:null, //  
        StatusPromoLink:false, //  \
        HashProodata:null,
        promokodeActive:false, //   
        promokodeError:{
          status:false,
          message:null
        }, // ,    
        promokodeErrorInfo:{
          errorSum:{
            countSum:0,
            status:false
          },
          errorProduct:{
            message:"    .     !",
            status:false
          },
          errorTimeActive:{
            countTimeActive:0,
            status:false
          },
          showIconError:false
        }, //          
        dataPromokodeDiscoun:{
          allSumDiscount:0,
          arrayProductDiscount:[]
        }, //         
        dataSumFirstPromokodeDiscoun:{
          allSumDiscount:0,
          arrayProductDiscount:[]
        }, //    

        typePayment:1,
        callOperator:1,
        preorderData: {
          dateDelivery: 0,
          timeDelivery: 0
        }, //      (0 -   )
        commentOrder:null,
        priceDeliverySelected: 0,
        allPointsSale:0,
        textKomment:null
      };

    case SET_UPDATE_APP_STATUS_REDUX:
      return {
        ...state,
        updateAppStatus: action.payload
      };
   case SET_KOMMENT_TEXT:
        return {
          ...state,
          textKomment: action.payload
        };

    case SET_UPDATE_APP_STATUS:
      return {
        ...state,
        updateAppStatus: action.payload.updateAppStatus,
        linkUpdateAppiOS:action.payload.linkUpdateAppiOS,
        linkUpdateAppAndroid:action.payload.linkUpdateAppAndroid,
        linkUpdateAppAndroidNoGoogle:action.payload.linkUpdateAppAndroidNoGoogle,
      };

    case SET_UPDATE_APP_STATUS_CANCEL:
      return {
        ...state,
        updateCancel: true
      };

    case UPDATE_ORDER_LIST_USER:
      return {
        ...state,
        orderListUser: action.payload,
      };

    case UPDATE_STATUS_PREORDER_DATA:
      return {
        ...state,
        statusLoadServerPreorderData: action.payload,
      };

    case UPDATE_LISTDATA_DELIVERY_DATA:
      return {
        ...state,
        listDataDelivery: action.payload,
        statusLoadServerPreorderData:true
      };

    case UPDATE_PREORDER_DATA:
      return {
        ...state,
        preorderData: action.payload,
      };

    case UPDATE_TYPE_PAYMENT:
      return {
        ...state,
        typePayment: action.payload,
      };
    case UPDATE_CALL_OPERATOR:
          return {
            ...state,
            callOperator: action.payload,
          };

    case PROMOKODE_UPDATE_DISCOUNT_INFO:
      return {
        ...state,
        dataPromokodeDiscoun: action.payload,
      };
    case PROMOKODE_UPDATE_SUM_FIRST:
          return {
            ...state,
            dataSumFirstPromokodeDiscoun: action.payload,
          };

    case PROMOKODE_UPDATE_ERROR_INFO:
      return {
        ...state,
        promokodeErrorInfo: action.payload,
      };

    case PROMOKODE_UPDATE_ERROR_UPDATE:
      return {
        ...state,
        promokodeError: action.payload,
      };

    case PROMOKODE_UPDATE:
      return {
        ...state,
        promokodeData: action.payload,
        StatusPromoLink:true,
        promokodeActive:action.status
      };


    case PROMOKODE_HASH_UPDATE:
      return {
        ...state,
        HashProodata: action.payload,
      };


    case POINTS_SALE_UPDATE:
      return {
        ...state,
        allPointsSale: action.payload,
      };


    case MODAL_STATUS:
      return {
        ...state,
        modalStateStatus: {...state.modalStateStatus,...action.payload},
      };


    case MODAL_CONTENT:
      return {
        ...state,
        modalStateContent: {...state.modalStateContent,...action.payload},
      };


      case RELOAD_PREORDER_DOPS:
      return {
        ...state,
        ListPreorderProductDops: action.payload,
      };

      case SET_STATUS_LOAD_AUTH_SCREEN:
      return {
        ...state,
        statusLoadingAuthScreen: action.payload,
      };

    case SET_TYPE_DELIVERY:
      return {
        ...state,
        typeDeliverySystem: action.payload,
        // basket:[]
      };

    case SET_ADRESS_AFFILIATE:
      return {
        ...state,
        adressAffiliate: action.payload,
        loadAdressAffiliate:true,
        affiliateSelect:null,
        activeAffiliate:action.activeAffiliate,
        textBlockAffiliateDeactive:action.textBlockAffiliateDeactive,
        activeDelivered:action.activeDelivered,
        displayAllAffiate:true,
        displayAll:true,
        textBlockDeliveredDeactive:action.textBlockDeliveredDeactive,
        typeDeliverySystem:action.typeDeliverySystem
      };

    case SET_SELECT_ADRESS:
      return {
        ...state,
        adressSelect: action.payload,
        displayAll:false
        // basket:[]
      };

    case SET_SELECT_AFFILIATE:
      return {
        ...state,
        affiliateSelect: action.payload,
        displayAllAffiate: false,
        // basket:[]
      };

    case SET_ADRESS_USER_SYSTEM:
      return {
        ...state,
        adressUser: action.payload,
        loadAdressUser:true,
        adressSelect:null
      };

    case SET_TIMEOUT_SEND_SMS:
      return {
        ...state,
        timeOutSendSms: action.payload
      };

      case SET_PHONENUMBER_KEYBOARD:
      return {
        ...state,
        phoneNumberKeyboard: action.payload
      };

      case SET_AUTH_USER_STATUS_PAGE:
      return {
        ...state,
        AuthUserStatus: action.payload,
      };

      case SET_AUTH_USER_STATUS_PAGE_PRELOADER:
      return {
        ...state,
        loadPageDefault:action.payload
      };

    case LOAD_POSTS:
      return {
        ...state,
        allPosts: action.payload,
        bookedPosts: action.payload.filter(post => post.booked),
        loading: false
      };
   case LOADING_PRODUCT_SCREEN:
      return {
        ...state,
        loadingProductScreen: false
      };
    case SET_ACTIVE_CATEGORY:
      return {
        ...state,
        activeCategoryProductList: action.payload,
        activeFilterCategory:null
      };
      case SET_ACTIVE_FILTER_CATEGORY:
      return {
        ...state,
        activeFilterCategory: action.payload
      };
    case SET_ACTIVE_SEARCH:
      return {
        ...state,
        searchActive: action.payload
      };
    case SET_POINTS_SYSTEM_SETTINGS:
      return {
        ...state,
        bonusPointsText: action.payload.bonusPointsText,
        bonusPointsActive:action.payload.bonusPointsActive,
        textRecommendBlockCart:action.payload.textRecommendBlockCart,
        textDopProductBlockCart:action.payload.textDopProductBlockCart,
        textPresetsGoodsBlockCart:action.payload.textPresetsGoodsBlockCart,
        dopInfoBlockBasket:action.payload.dopInfoBlockBasket,
        positionOrdersBlock:action.payload.positionOrdersBlock,
      };
    case SET_BDAY_STATUS:
      return {
        ...state,
        BDay: action.payload
      };
    case SET_BDAY_ALL_INFO:
      // console.log("action BDay",action)
      return {
        ...state,
        // BDayAllInfo:{
        //   BDayActivity:action.payload.BDayAllInfo.BDayActivity,//       
        //   BDayCountBefore:action.payload.BDayAllInfo.BDayCountBefore,// -     
        //   BDayCountAfter:action.payload.BDayAllInfo.BDayCountAfter,// -     
        //   BDayDiscountPrice:action.payload.BDayAllInfo.BDayDiscountPrice,//    
        //   BDay_action_adition:action.payload.BDayAllInfo.BDay_action_adition,//    
        //   BDay_gift_adition:action.payload.BDayAllInfo.BDay_gift_adition,//    
        //   BDay_threeForTwo_adition:action.BDayAllInfo.payload.BDay_threeForTwo_adition,//  3=2  
        // }
        BDayAllInfo:action.payload.BDayAllInfo
      };
    // case SET_BDAY_ALL_INFO:
    //   return {
    //     ...state,
    //     BDayAllInfo: action.payload
    //   }; //   
    case SET_BDAY_DISCOUNT:
      // console.log("BDAY_DISCOUNT",action)
      return {
        ...state,
        BDayDiscount: action.payload.BDayDiscount
    };
      case SET_BDAY_PRICE:
      return {
        ...state,
        BDayPrice: action.payload
    };
    case SET_AUTH_USER_DATA:
      return {
        ...state,
        AuthUserData: action.payload
      };
    case SET_TOKEN_USER_FRAME:
      return {
        ...state,
        AuthUserData: {...state.AuthUserData,...action.payload}
      };

    case SET_USER_ID_VK:
      return {
        ...state,
        idUserVK: action.payload
      };
    case ADD_PRODUCT_BASKET:
      //console.log(action.payload);
      return {
        ...state,
        basket: action.payload,
      };

    case ADD_PRODUCT_BASKET_OFF:
      //console.log(action.payload);
      return {
        ...state,
        basketOff: action.payload,
      };

    case RELOAD_BASKET_SUM:
      //console.log(action.payload);
      return {
        ...state,
        basketSum: action.payload
      };


    case SET_STATUS_RELOAD_CATEGORY:
      //console.log(action.payload);
      return {
        ...state,
        reloadedCatalog: action.payload
      };

    case SET_CITY_ID:
      return {
        ...state,
        cityData: action.payload,
        loadingCatalog:true,
        cityReloadSystem:true,
        basket:[],
        basketTopping:[],
        basketSum:0,
        cityLoadPhone: false
      };

      case SET_CITY_LIST:
      return {
        ...state,
        cityList: action.payload,
      };
      case RELOAD_CITY_DATASET:
            return {
              ...state,
              cityLoadPhone: false
            };

      case ADD_PRODUCT_BASKET_TOPPING:
      //console.log(action.payload);
      return {
        ...state,
        basketTopping: action.payload
      };

    case LOAD_CATALOG:
      return {
        ...state,
        allCategory: action.payload.Category,
        allProducts: action.payload.Catalog,
        allRecommend: action.payload.CatalogRecommend,
        PreorderCategoryDopsLimit: action.payload.PreorderCategoryDopsLimit,
        AllDopProduct: action.payload.CatalogDopProduct,
        loadingCatalog: false,
        cityReloadSystem:false
      };
    case LOAD_SLIDER:
      return {
        ...state,
        allSlide: action.payload,
        loadingSlider: false
      };

      case LOAD_SLIDER_STATUS:
      return {
        ...state,
        loadingSlider: true
      };
    case TOGGLE_BOOKED:
      const allPosts = state.allPosts.map(post => {
        if (post.id === action.payload) {
          post.booked = !post.booked
        }
        return post
      });

      return {
        ...state,
        allPosts,
        bookedPosts: allPosts.filter(post => post.booked)
      };
    case REMOVE_POST:
      return {
        ...state,
        allPosts: state.allPosts.filter(p => p.id !== action.payload),
        bookedPosts: state.bookedPosts.filter(p => p.id !== action.payload)
      };
    case ADD_POST:
      return {
        ...state,
        allPosts: [{ ...action.payload }, ...state.allPosts]
      };
    case SET_CITY_DATABASE:
      return {
        ...state,
        cityLoadPhone: false
      };
    case ACTIVE_ORDER_SCREEN_RELOAD:
      return {
        ...state,
        activeOrder:{
          idOrder:action.payload.idOrder,
          statusReload:action.payload.statusReload
        }
      };
    default:
     // console.log('   ',action.type);
      return state;
  }
}
